.search__inner {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding: 16px 10px;
  box-sizing: border-box;
}

.search__form {
  display: flex;
}

.search__input {
  padding: 8px 12px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 2px;
}

.search__button {
  font-size: 16px;
  cursor: pointer;
  padding: 8px 12px;
  margin-left: 4px;
  background-color: rgb(22, 107, 198);
  color: #ffffff;
  border: none;
  border-radius: 2px;
}

.search__button:hover {
  background-color: #1565c0;
}

.search__button:disabled {
  cursor: not-allowed;
  opacity: 0.75;
}

.search__error {
  display: flex;
  align-items: center;
  margin-left: 8px;
  color: #b94a48;
}

.info__inner {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding: 0 10px;
  box-sizing: border-box;
}

.info__table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  table-layout: fixed;
  border: 1px solid #cbcbcb;
}

.info__item {
  border-bottom: 1px solid #cbcbcb;
}

.info__item-name,
.info__item-value {
  padding: 8px 12px;
  vertical-align: top;
  overflow: hidden;
  text-overflow: ellipsis;
}

.info__item-name {
  border-right: 1px solid #cbcbcb;
}

.footer {
  border-top: 1px solid #ccc;
}

.footer__inner {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding: 16px 10px;
  text-align: right;
  box-sizing: border-box;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.app__content {
  flex: 1 0 auto;
}

.app__info {
  margin-bottom: 16px;
}

.app__footer {
  flex: 0 0 auto;
}

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

